@media screen and (min-width: 60em) {
	.input-list-item.field-grid-item-1-2 {
		width: 25%;
	}
}

.nav > li > [data-helper="Error"], .nav > li > [data-helper="Sitemap"], .dropzone [title="Error"], .dropzone [title="Sitemap"]
 { display: none; }

.text p {
	margin: 0;
}

.text p + p {
	margin-top: 24px;
	margin-top: 1.5rem;
}

#force-update {
	font-weight: 900;
	text-decoration: underline;
	cursor: pointer;
}

#update-container {
	overflow: visible;
	position: relative;
	height: 128px;
	height: 8rem;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: -webkit-box;
	display: flex;
	-webkit-align-items: center;
	    -ms-flex-align: center;
	        -webkit-box-align: center;
	        align-items: center;
	-webkit-justify-content: center;
	    -ms-flex-pack: center;
	        -webkit-box-pack: center;
	        justify-content: center;
}

#update-container .help-text {
	display: block;
}

#update-container .loader-spinner {
	display: none;
}

#update-container .updated-help {
	display: none;
}


.updated-help {
	font-style: italic;
	display: none;
}



#update-container .loader-spinner {
	position: absolute;
	top: calc(50% - 1.5rem);
	left: calc(50% - 1.5rem);
	width: 48px;
	width: 3rem;
	height: 48px;
	height: 3rem;
	border: 3px solid #373737;
	border: 3px solid rgba(55, 55, 55, .3);
	border-radius: 50%;
	border-top-color: rgba(55, 55, 55, .8);
	-webkit-animation: 1.5s spin ease-in-out infinite;
	        animation: 1.5s spin ease-in-out infinite;
}

#update-container.thinking .help-text {
	display: none;
}

#update-container.thinking .loader-spinner {
	display: block;
}

#update-container.thinking .updated-help {
	display: none;
}

#update-container.updated {
	text-align: center;
}

#update-container.updated .help-text {
	display: none;
}

#update-container.updated .loader-spinner {
	display: none;
}

#update-container.updated .updated-help {
	display: block;
}

@-webkit-keyframes spin {
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.list__image {
	max-width: 80px;
	max-width: 5rem;
}
